import React from 'react';
import {
  Box
} from "@chakra-ui/react";
import { ColorBox } from "../../library/CustomUI.js";
import { dateTime } from "../../functions/generic.js";

const boxStyles = {
  padding: "10px 25px",
  marginY: "10px",
  borderRadius: "5px",
  backgroundColor: "surface.500",
};

const InfoContainer = ({ transit }) => {
  return (
    <Box {...boxStyles} >
      <ColorBox identifier="MAG" text={transit.V} />
      <ColorBox identifier="PERCENT VISIBLE" text={transit.percent_transit_observable + "%"} />
      <ColorBox identifier="DEPTH" text={transit["depth(ppt)"] + " ppt"} />
      <ColorBox identifier="STARTS" text={dateTime(transit["start time"]).time} />
      <ColorBox identifier="ENDS" text={dateTime(transit["end time"]).time} />
      <ColorBox identifier="DURATION" text={transit["duration(hours)"] + " Hr"} />
    </Box>
  );
}

export default InfoContainer;