import React from 'react';
import {
  FormControl,
  FormLabel,
  Text
} from "@chakra-ui/react"
import { Input, Button, H1 } from '../library/CustomUI.js';
import {
  faLock
} from "@fortawesome/free-solid-svg-icons";
import { POST } from '../functions/post.js';
import * as paths from '../paths.js';

class ChangePasswordForm extends React.Component {
  state = {
    password1: '',
    password2: '',
    error: ''
  };

  setField = (value, field) => {
    this.setState({
      [field]: value
    });
  }

  handleForm = () => {
    let loc = window.location.pathname;
    if (loc.split("/").length > 4) {
      const token = loc.split("/")[3];
      const uid = loc.split("/")[2];
      console.log(uid);
      if (this.state.password1 && this.state.password2) {
        POST(paths.PASSWORD_RESET_CONFIRM_API,
          {
            new_password1: this.state.password1,
            new_password2: this.state.password2,
            uid: uid,
            token: token
          }
        ).then(res => {
          console.log(res);
        });
      }
      else {
        this.setState({
          error: "All fields required"
        })
      }
    }
  }

  render() {
    return (
      <form>
        <H1 mb={10}>Change Password</H1>

        {this.state.error &&
          <Text color="error.500" mt={5}>{this.state.error}</Text> }

        <FormControl id="username" mt={5}>
          <FormLabel>New Password</FormLabel>
          <Input
            i={faLock}
            onChange={event =>
              this.setField(event.target.value, 'password1')}
            placeholder="Password..."
            autoComplete="true" />
        </FormControl>

        <FormControl id="username" mt={5}>
          <FormLabel>Repeat Password</FormLabel>
          <Input
            i={faLock}
            onChange={event =>
              this.setField(event.target.value, 'password2')}
            placeholder="Confirm Password..."
            autoComplete="true" />
        </FormControl>

        <FormControl id="submit" mt={5} mb={5} textAlign="center">
          <Button onClick={this.handleForm}>
            Change
          </Button>
        </FormControl>
      </form>
    );
  }
}

export default ChangePasswordForm;