import React from 'react';
import {
  FormControl,
  FormLabel,
  Text
} from "@chakra-ui/react"
import { loginUser } from '../functions/auth.js';
import { Input, Button, H1 } from '../library/CustomUI.js';
import {
  faUser,
  faLock
} from "@fortawesome/free-solid-svg-icons";

class Login extends React.Component {
  state = {
    username: '',
    password: '',
    error: ''
  };

  setField = (value, field) => {
    this.setState({
      [field]: value
    });
  }

  handleForm = () => {
    loginUser(
      this.state.username,
      this.state.password,
      this.props.login,
      this.setState.bind(this)
    ).then(res => {
      if (res)
        this.setState({
          error: res.error
        });
    });
  }

  render() {
    return (
      <form>
        <H1 mb={10}>Login</H1>

        {this.state.error &&
          <Text color="error.500" mt={5}>{this.state.error}</Text> }

        <FormControl id="username" mt={5}>
          <FormLabel>Username</FormLabel>
          <Input
            i={faUser}
            onChange={event =>
              this.setField(event.target.value, 'username')}
            placeholder="Username..."
            autoComplete="true" />
        </FormControl>

        <FormControl id="password" mt={5} mb={5}>
          <FormLabel>Password</FormLabel>
          <Input
            i={faLock}
            onChange={event =>
              this.setField(event.target.value, 'password')}
            type="password"
            placeholder="Password..."
            autoComplete="true" />
        </FormControl>

        <FormControl id="submit" mt={5} mb={5} textAlign="center">
          <Button onClick={this.handleForm}>
            Login
          </Button>
        </FormControl>
      </form>
    );
  }
}

export default Login;
