import React from "react";
import Search from "../components/Search/Search.js";
import {
  Box
} from "@chakra-ui/react";
import Transit from "../components/Transit/Transit";
import { parse, POST } from '../functions/post.js';
import { Spinner, H1 } from '../library/CustomUI.js';
import { TRANSITS_API } from '../paths.js';
import { currentDate } from '../functions/generic.js';

const day = currentDate().day;
const month = currentDate().month;
const year = currentDate().year;
const full_date = currentDate().full_date;

const boxAttrs = {
  className: "transits",
  display: "flex",
  flexWrap: "wrap",
  justifyContent: "space-between",
  maxWidth: "800px",
  margin: "0 auto"
};

class TransitList extends React.Component {
  ismounted = false;

  state = {
    transits: [],
    search_results: []
  };

  fetchTransits = () => {
    let data = {};
    let user = JSON.parse(localStorage.getItem("user"));

    if (user)
      data = { lat: user.lat, lng: user.lng };
    
    POST(TRANSITS_API, data).then(res => {
      if (this.ismounted) {
        console.log(data);
        // Filter for upcoming transits only
        this.filterToday(parse(res.data.Transits));
      }
    });
  }

  setTransits = (transits) => {
    this.setState({
      search_results: transits
    }, () => console.log("NEW LIST: "+ this.state.search_results));
  }

  componentDidMount() {
    this.ismounted = true;
    this.fetchTransits();
  }

  componentWillUnmount() {
    this.ismounted = false;
  }

  filterToday = (transits) => {
    let tonights = [];
    let remaining = [];

    transits.forEach(t => {
      let isTonight = t["start time"].split(" ")[0] === full_date;
      let isInFuture =
        (t["start time"].split("-")[0] > year) ||
        (t["start time"].split("-")[1] > month) ||
        (t["start time"].split("-")[2] > day);

      if (isTonight) {
        tonights.push(t);
      }
      else if (isInFuture) {
        remaining.push(t);
      }
      else {
        console.log(year, month, day)
      }
    });

    this.setState({
      transits: tonights.concat(remaining),
      search_results: tonights.concat(remaining)
    });
  }

  render() {
    return (
      <React.Fragment>
        <H1>Transits</H1>

        <Search returnTransits={this.setTransits} search_results={this.state.search_results} transits={this.state.transits.slice()} />

        {!this.ismounted ?
          <Spinner />

          :

          <Box {...boxAttrs} >
            {this.state.search_results.slice().splice(0, 55).map((transit, index) =>
              <Transit key={index} data={transit} auth={this.props.auth} />)}
          </Box>
        }
      </React.Fragment>
    );
  }
}

export default TransitList;